<script>
    import { mapGetters } from 'vuex';

    export default {
        computed: {
            isHighlighted() {
                return this.profile.new_quiz && this.profile.new_quiz.quiz_token;
            },
            color() {
                return this.isHighlighted ? 'white' : 'black';
            },
            ...mapGetters({
                profile: 'quizzes/profile',
            }),
        },
        methods: {
            goToQuizSection() {
                this.$router.push({ path: '/quiz' });
